<template>
  <div>
    <table class="table">
      <thead class="text-center">
        <th>
          Daily Summary
          <span class="ml-4"> {{ day }}- {{ month }}- {{ year }}</span>
        </th>
      </thead>
      <tbody>
        <tr>
          <td scope="row" class="top">Total Orders</td>
          <td scope="row">10</td>
        </tr>
        <tr>
          <td scope="row">Total Sales</td>
          <td>10</td>
        </tr>
        <tr>
          <td scope="row">Pending Orders</td>
          <td>4</td>
        </tr>
        <tr>
          <td scope="row">Delivered Orders</td>
          <td>15</td>
        </tr>
        <tr>
          <td scope="row">Canceled Orders</td>
          <td>16</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      day: "",
      month: "",
      year: "",
    };
  },
  methods: {
    getDay: function () {
      var d = new Date();
      this.day = d.getDate();
      this.month = d.getMonth();
      this.year = d.getFullYear();
    },
  },
  mounted() {
    this.getDay();
  },
};
</script>

<style  scoped>
table {
  background-color: #f8e7df;
  color: #000;
  /* box-shadow: 0px 0px 1px 1px #cc7d5e; */
}
.table td:nth-child(1) {
  border-right: 2px solid #cc7d5e;
}
.table td {
  padding: 0.75rem;
  vertical-align: top;
  border-top: 1px solid #cc7d5e;
}
.top {
  border-top: 1px solid #fff;
}
thead th {
  border-bottom: 1px solid #cc7d5e;
}
</style>