<template>
  <div class="bg-white main p-3">
    <div class="d-flex align-items-center justify-content-between">
      <div class="">
        <h6>Order Summary</h6>
        <p>Your Summarized Order Summary</p>
      </div>
      <div class="button-container">
        <button @click="monthlyShow" :class="{ buttonAct: buttonActive }">
          Monthly</button
        ><button @click="weeklyShow" :class="{ buttonActTwo: buttonActiveTwo }">
          Weekly</button
        ><button
          @click="dailyShow"
          :class="{ buttonActThree: buttonActiveThree }"
        >
          Today
        </button>
      </div>
    </div>
    <div class="my-2">
      <MonthlySummary v-if="monthly" />
      <WeeklySummary v-if="weekly" />
      <DailySummary v-if="daily" />
    </div>
  </div>
</template>

<script>
import DailySummary from "../views/DailySummary";
import WeeklySummary from "../views/WeeklySummary";
import MonthlySummary from "../views/MonthlySummary";
export default {
  data() {
    return {
      monthly: false,
      weekly: false,
      daily: true,
      buttonActive: false,
      buttonActiveTwo: false,
      buttonActiveThree: true,
    };
  },
  methods: {
    monthlyShow: function () {
      this.daily = false;
      this.weekly = false;
      this.monthly = true;
      this.buttonActive = true;
      this.buttonActiveTwo = false;
      this.buttonActiveThree = false;
    },
    dailyShow: function () {
      this.daily = true;
      this.weekly = false;
      this.monthly = false;
      this.buttonActive = false;
      this.buttonActiveTwo = false;
      this.buttonActiveThree = true;
    },
    weeklyShow: function () {
      this.daily = false;
      this.weekly = true;
      this.monthly = false;
      this.buttonActive = false;
      this.buttonActiveTwo = true;
      this.buttonActiveThree = false;
    },
  },

  components: {
    DailySummary,
    WeeklySummary,
    MonthlySummary,
  },
};
</script>

<style scoped>
.main {
  height: 420px;
  padding: 1rem !important;
}
.button-container {
  background-color: #f8f3f7;
  padding: 0.1rem;
  border-radius: 0.2rem;
}
button {
  background-color: #f8f3f7;
  border: none;
  outline: none;
  padding: 0.4rem;

  color: #aaa8ab;
}
.buttonAct {
  background-color: #fff;
  border-radius: 0.4rem;
  color: #000;
  font-weight: 600;
}
.buttonActTwo {
  background-color: #fff;
  border-radius: 0.4rem;
  color: #000;
  font-weight: 600;
}
.buttonActThree {
  background-color: #fff;
  border-radius: 0.4rem;
  color: #000;
  font-weight: 600;
}
.order h6 {
  font-weight: 600;
  font-size: larger;
}
.order p {
  font-size: small;
  color: #ccc9cb;
}
@media (max-width: 674px) {
  /* .d-flex {
    flex-direction: column;
  } */
  .p-5 {
    padding: 0.5rem !important;
  }
